import React, { memo, useEffect, useState } from 'react';
import { useStoreon } from 'storeon/react';
import CatalogPageLayoutContainer from './CatalogPageLayout/CatalogPageLayoutContainer';

const CatalogPage = ({context}) => {
  const {    
    breadcrumbs,
    categories,
    page_info,
    profile,
  } = context;
  const [newContentn, setNewContent] = useState('');
  const { productStore, isMobile, recommendedProductsStore, productYouAlredyWatchStore ,currency } = useStoreon('currency','productStore', 'recommendedProductsStore', 'productYouAlredyWatchStore', 'isMobile');
  const { content } = page_info;
  const { role } = profile;
  const isLoadMainContentCatalog = context?.isLoadMainContentCatalog;
  let timer;
  useEffect(()=>{
    // timer = setTimeout(()=>{
      setNewContent(content)
    // },1500);
    return function cleanup(){
      // clearTimeout(timer);
    }
  },[content])
  return (
    <React.Fragment>
      <CatalogPageLayoutContainer
        breadcrumbs={breadcrumbs}
        currentPage={productStore?.currentPage}
        categories={categories}
        currency={currency}
        isLoadMainContentCatalog={isLoadMainContentCatalog}
        dataProducts={productStore}//dataProducts}
        isLoading={productStore?.isLoadingCatalog}
        isLoadingMyWish={productStore.isLoadingMyWish}
        isMobile={isMobile}
        youAlredyWatch={productYouAlredyWatchStore}
        isLoadingMyWishYouAlredy={productYouAlredyWatchStore.isLoadingMyWish}

        recommendedProducts={recommendedProductsStore}
        isLoadingMyWishRecomended={recommendedProductsStore.isLoadingMyWish}

        content={newContentn}
        role={role}
      />
    </React.Fragment>
  )
}

export default React.memo(CatalogPage);
